    updateorder
  }
})

export default class AddGoods extends Vue {

  @Prop({ required: false })
  orderList: any;
  @Prop({ required: false })
  pagetype: string;

  order = {};
  doReminder(row) {
    // this.$confirm("确定要发送一次催单?", "提示", {
    //   confirmButtonText: "确定",
    //   cancelButtonText: "取消",
