    let a: any = this.$refs.updateorder;
    a.model = true;
    //  a.queryPresDrugback(row.presId)
    this.getOrderDetail(row.presId);
  }
  getOrderDetail(presId) {
    indexApi
      .getOrderInfo({
        presId: presId
      })
      .then(res => {
        if (res["retCode"]) {
          this.order = res.data;
          this.updateOrder = res.data;
        } else {
          if (!res["islogin"]) {
            this.$alert(res["message"]);
          }
          console.error("数据查询错误");
        }
      });
  }
  updateOrder = {};
  updateInfo(row) {
 
    let a: any = this.$refs.updateorder;
    a.formModel = true;
  
    this.getOrderDetail(row.presId);
      
  }

  getOrderList() {
    this.$emit("getOrderList");
  }
  handlePayStatus(status) {
    switch (status) {
      case "PAY_WAIT":
        return "支付成功";
      case "PAY_SUCCESS":
        return "支付成功";
      default:
        return "";
